<template>
  <td class="text-right">
    <v-progress-circular 
      v-if="moduleDataLoading"
      width="3"
      color="grey lighten-1"
      indeterminate
      size="18"
    ></v-progress-circular>
    <span v-else class="mr-2">
      <span v-show="!editExamTime">{{ examTime.value }}</span>
      <v-text-field
        v-model="examTime.value"
        dense
        :class="{ 'd-inline-block': editExamTime }"
        class="text-center"
        style="width: 25px"
        v-show="editExamTime"
        hide-details="true"
        @keyup.enter="saveTime"
        id="exam_passing_score"
        type="text"
        @keypress="isNumber($event)"
      ></v-text-field>
    </span>
    <v-tooltip v-if="!editExamTime" right>
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          v-bind="attrs"
          v-on="on"
          icon
          small
          @click="updateTime"
        >
          <v-icon
            size="large"
            color="blue darken-3"
          >
            mdi-pencil-box-outline
          </v-icon>
        </v-btn>
      </template>
      <span>Update</span>
    </v-tooltip>
    <div class="d-inline-block" v-else>
      <v-tooltip v-if="!saveExamPassingScoreLoading" right>
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            v-bind="attrs"
            v-on="on"
            icon
            small
            @click="saveTime"
          >
            <v-icon
              size="large"
              color="green darken-3"
            >
              mdi-content-save
            </v-icon>
          </v-btn>
        </template>
        <span>Save</span>
      </v-tooltip>
      <v-btn
        v-else
        icon
        small
        loading
      ></v-btn>
      <v-tooltip right>
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            v-bind="attrs"
            v-on="on"
            icon
            small
            @click="cancelTime"
          >
            <v-icon
              size="large"
              color="red darken-3"
            >
              mdi-close-circle-outline
            </v-icon>
          </v-btn>
        </template>
        <span>Cancel</span>
      </v-tooltip>
    </div>
  </td>
</template>

<script>
import { mapState, mapActions } from "vuex"

export default {
  data: () => ({
    editExamTime: false,
    currentExamTime: null
  }),
  computed: {
    ...mapState("settings", ["moduleData", "moduleDataLoading", "saveExamPassingScoreLoading", "saveExamPassingScoreError"]),
    examTime() {
      return this.moduleData.find(({ id }) => id === '3')
    }
  },
  methods: {
    ...mapActions("settings", ["saveExamPassingScore"]),
    updateTime() {
      if (!this.moduleDataLoading) {
        this.editExamTime = true
        this.currentExamTime = this.examTime.value
        
        setTimeout(() => {
          const exam_time = document.getElementById('exam_passing_score')
          const examTimeLength = exam_time.value.length

          exam_time.focus()
          exam_time.setSelectionRange(examTimeLength, examTimeLength)
        }, 100)
      }
    },
    async saveTime() {
      if (!this.saveExamPassingScoreLoading) {
        await this.saveExamPassingScore({ count: this.examTime.value })

        if (!this.saveExamPassingScoreError) {
          this.currentExamTime = this.examTime.value
          this.cancelTime()
        }
      }
    },
    cancelTime() {
      if (!this.saveExamPassingScoreLoading) {
        this.editExamTime = false
      }

      if (this.examTime.value != this.currentExamTime) {
        this.examTime.value = this.currentExamTime
      }
    },
    isNumber(evt) {
      evt = (evt) ? evt : window.event
      var charCode = (evt.which) ? evt.which : evt.keyCode
      if ((charCode > 31 && (charCode < 48 || charCode > 57))) {
        evt.preventDefault()
      } else {
        return true
      }
    }
  }
}
</script>